    getWaterCorrections: function(){
      let waterDataCorrected = waterData.map(el => ({...el}));
      for(let i=0;i<waterData.length;i++){
        let el = waterDataCorrected[i];
        el.Toiletsgal = (this.toiletKfactor*(el.IntoCompostersgal + (1/this.toiletKfactor-1)*el.Biosolidsgal));
        el.Sinksgal = (this.faucetKfactor*el.Sinksgal);
        el.EnteringBuildinggal = (el.Toiletsgal + el.Miscellaneousgal + el.Showersgal + el.Sinksgal + el.WaterFountainsgal);
        if(i==0){
          //if first iteration, use the user specified cistern capacity
          el.CisternVolumegal = (1*this.cisternCapacity);
        }else{
          //else need to use previous cistern volume corrected number
          el.CisternVolumegal = (waterDataCorrected[i-1].CisternVolumegal);
        };
        // first take demand from rain
        el.rainBeingFiltered = Math.min(el.EnteringBuildinggal/(1-0.050200803), el.Rainbeingcapturedgal);
        el.rainToIrrigation = Math.min(el.Rainbeingcapturedgal - el.rainBeingFiltered, el.CisterntoCondensategal);
        el.rainBeingUsed = el.rainBeingFiltered + el.rainToIrrigation;
        // if rain isn't enough, then take from cistern
        // negative cistern flow indicates flowing out of cistern
        el.CisternToFiltrationgal = -1*Math.min(el.EnteringBuildinggal/(1-0.050200803) - el.rainBeingFiltered, el.CisternVolumegal);
        el.CisternToIrrigation = -1*Math.min(el.CisterntoCondensategal - el.rainToIrrigation, el.CisternVolumegal + el.CisternToFiltrationgal);
        el.CisternFlowgal = el.CisternToFiltrationgal + el.CisternToIrrigation;
        // if rain and cistern isn't enough, take from city water
        el.CityWatergal = Math.max(el.EnteringBuildinggal + el.CisterntoCondensategal - (el.rainBeingFiltered + -1*el.CisternToFiltrationgal)*(1-0.050200803) - (el.rainToIrrigation + -1*el.CisternToIrrigation),0);
        // total amount of water getting filtered
        // console.log(el.CisternFlowgal)
        // console.log(el.rainBeingFiltered)
        el.EnteringFiltrationgal = Math.max(-1*el.CisternToFiltrationgal,0) + el.rainBeingFiltered;
        // amount of water lost during filtration
        el.FiltrationBlowdowngal = 0.050200803*el.EnteringFiltrationgal;
        // if rain exceeds demand, there will be overflow
        // overflow first goes to filling up cistern
        el.CisternFlowgal = el.CisternFlowgal + Math.min(el.Rainbeingcapturedgal - el.rainBeingUsed,1*this.cisternCapacity-el.CisternVolumegal);
        // if water is going into cistern then account for that being taken from rain
        el.rainToCistern = Math.max(el.CisternFlowgal,0);
        el.rainBeingUsed = el.rainBeingUsed + el.rainToCistern;
        el.CisternVolumegal = el.CisternVolumegal + el.CisternFlowgal;
        // if overflow rain exceeds cistern then it goes to irrigation
        el.IrrigationOverflowgal = Math.max(el.Rainbeingcapturedgal - el.rainBeingUsed,0);
      }

      return waterDataCorrected;
    },
    getWaterTotals: function(){
      // console.log(this.filterData)
      let waterTotals = [];
      waterTotals.treatmentData = this.filterData.reduce((total,arr)=>total+arr.FiltrationBlowdowngal+arr.Sinksgal+arr.Showersgal+arr.Miscellaneousgal+arr.WaterFountainsgal-arr.Consumptiongal,0);
      waterTotals.toiletData = this.filterData.reduce((total,arr)=>total+arr.Toiletsgal,0);
      waterTotals.sinkData = this.filterData.reduce((total,arr)=>total+arr.Sinksgal,0);
      waterTotals.potableWaterTankData = this.filterData.reduce((total,arr)=>total+arr.EnteringBuildinggal,0);
      waterTotals.overflowData = this.filterData.reduce((total,arr)=>total+arr.IrrigationOverflowgal,0);
      waterTotals.cityWaterData = this.filterData.reduce((total,arr)=>total+arr.CityWatergal,0);
      waterTotals.filtrationData = this.filterData.reduce((total,arr)=>total+arr.EnteringFiltrationgal,0);
      // waterTotals.cisternFlowData = waterTotals.overflowData + waterTotals.filtrationData + this.filterData.reduce((total,arr)=>total+arr.CisterntoCondensategal,0);
      // console.log(this.filterData)
      // waterTotals.cisternFlowData = this.filterData[0].CisternVolumegal-this.filterData[this.filterData.length-1].CisternVolumegal;
      waterTotals.cisternFlowData = this.filterData.reduce((total,arr)=>total+arr.CisternFlowgal,0);
      // waterTotals.rainUsedData = this.filterData.reduce((total,arr)=>total+arr.rainBeingFiltered+arr.rainToCistern,0)-waterTotals.cisternFlowData;
      waterTotals.rainCollectedData = this.filterData.reduce((total,arr)=>total+arr.Rainbeingcapturedgal,0);
      waterTotals.rainToOverflowData = this.filterData.reduce((total,arr)=>total+arr.IrrigationOverflowgal,0);
      let irrigationNeeded = this.filterData.reduce((total,arr)=>total+arr.CisterntoCondensategal,0);
      waterTotals.rainToIrrigationData = Math.min(waterTotals.rainCollectedData - waterTotals.rainToOverflowData,irrigationNeeded);
      waterTotals.rainToFiltrationData = waterTotals.rainCollectedData - waterTotals.rainToOverflowData - waterTotals.rainToIrrigationData - Math.max(waterTotals.cisternFlowData,0);
      waterTotals.cisternToIrrigationData = Math.min(Math.max(-1*waterTotals.cisternFlowData,0),irrigationNeeded - waterTotals.rainToIrrigationData);
      waterTotals.cisternToFiltrationData = Math.max(-1*waterTotals.cisternFlowData,0) - waterTotals.cisternToIrrigationData;
      waterTotals.cityWaterToIrrigationData = Math.min(waterTotals.cityWaterData,irrigationNeeded - waterTotals.rainToIrrigationData - waterTotals.cisternToIrrigationData);
      waterTotals.cityWaterToFiltrationData = waterTotals.cityWaterData - waterTotals.cityWaterToIrrigationData;
      // console.log('Irrigation Needed: ' + irrigationNeeded)
      // console.log('')
      return waterTotals;
    },
  },
