<template>
  <v-layout row wrap>
    <v-flex xs12>
      <v-card>
        <v-navigation-drawer class="mr-0 pr-0" :mini-variant.sync='mini' app stateless hide-overlay clipped v-model='drawer' width=400>
          <v-toolbar flat dark color='secondary'>
            <v-toolbar-side-icon @click.stop="mini=!mini,items.forEach((el,i)=>{if(mini==true){items[i].active=0}else{items[i].active=1}})" large/>
            <v-toolbar-title>Simulation Input</v-toolbar-title>
            <v-spacer/>
            <v-btn flat icon @click.stop="mini=!mini,items.forEach((el,i)=>items[i].active=0)">
              <v-icon>chevron_left</v-icon>
            </v-btn>
          </v-toolbar>
          <v-list expand>
            <v-list-group
              pt-0 mt-0
              v-for="item in items"
              :key="item.title"
              v-model="item.active"
              :prepend-icon="item.action"
              no-action
            >
              <template v-slot:activator>
                <v-list-tile>
                  <v-list-tile-content>
                    <v-list-tile-title class='title'>{{ item.title }}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </template>

              <v-list-tile
                v-for="subItem in item.items"
                :key="subItem.title"
                class="scrollable scrollbar scrollVisible"
              >
                <v-flex xs12>
                  <v-list-tile-content class="scrollable scrollbar scrollVisible">
                    <v-text-field class='' v-if="subItem.type=='input'" v-model=subItem.value @change="inputChange(subItem)" type="number" :label=subItem.title></v-text-field>
                    <v-text-field class='' v-else-if="subItem.type=='input-text'" v-model=subItem.value type="text" :label=subItem.title></v-text-field>
                    <v-layout justify-center row wrap v-else-if="subItem.type=='select'">
                      <v-flex xs9>
                        <v-tooltip bottom>
                          <template v-slot:activator="{on}">
                            <!-- :class=subItem.color -->
                            <v-combobox v-model=subItem.value v-on="on" :label=subItem.title :items=subItem.items @change="(a)=>{subItem.efficiency = a.value; inputChange(subItem)}"></v-combobox>
                          </template>
                          <span class=''>{{subItem.value.text}}</span>
                        </v-tooltip>
                      </v-flex>
                      <v-spacer/>
                      <v-flex xs3 text-xs-center>
                        <!-- :class=subItem.color -->
                        <v-text-field type='number' min="0.01" v-model=subItem.efficiency :label=subItem.units  @input="inputChange(subItem)"></v-text-field>
                      </v-flex>
                    </v-layout>
                    <v-select v-on="on" class="scrollVisible" v-model=subItem.value v-else-if="subItem.type=='select2'" :items=subItem.items :label=subItem.title></v-select>
                  </v-list-tile-content>
                </v-flex>
                <v-list-tile-action>
                  <v-icon>{{ subItem.action }}</v-icon>
                </v-list-tile-action>
              </v-list-tile>
            </v-list-group>
          </v-list>
        </v-navigation-drawer>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data: () => ({
    drawer: true,
    mini: false,
    items: [
          {
            active: 1,
            action: 'map',
            title: 'Environment',
            items: [
              { title: 'Location',
                type: 'select2',
                items: ['Asheville, NC','Atlanta, GA','Boston, MA','Houston, TX','Los Angeles, CA','Miami, FL','Pheonix, AZ','Seattle, WA','Beijing, CN','Lagos, NG','Paris, FR'],
                value: 'Atlanta, GA'
              },
              {
                title: 'Base Year',
                type: 'select2',
                min: '1900',
                max: '2019',
                items: ['1950','1960','1970','1980','1990','2000','2010','2017','2018','2019','2050'],
                value: '2017'
              },
              { title: 'Climate Change',
                type: 'select2',
                min: '-5',
                max: '5',
                //items: ['+5 \xB0F','+4 \xB0F','+3 \xB0F','+2 \xB0F','+1 \xB0F','\xB10 \xB0F','-1 \xB0F','-2 \xB0F','-3 \xB0F','-4 \xB0F','-5 \xB0F'],
                items: ['No Impact','Low Impact: +1 \xB0F','Medium Impact: +2 \xB0F','High Impact: +4 \xB0F'],
                value: 'No Impact'
              },
              { title: 'Climate Change \xB0F',
                type: 'select2',
                min: '-5',
                max: '5',
                items: ['+5 \xB0F','+4 \xB0F','+3 \xB0F','+2 \xB0F','+1 \xB0F','\xB10 \xB0F','-1 \xB0F','-2 \xB0F','-3 \xB0F','-4 \xB0F','-5 \xB0F'],
                value: '\xB10 \xB0F'
              },
            ]
          },
          {
            active: 1,
            action: 'location_city',
            title: 'Building Characteristics',
            items: [
              { title: 'Gross Area (sqft)',
                type: 'input',
                value: 20000
              },
              { title: 'Building Type',
                type: 'select2',
                items: ['Residential','Office','Educational','Healthcare'],
                value: 'Residential',
              },
              { title: 'Usage',
                type: 'select2',
                items: ['High','Standard','Low'],
                value: 'Standard'
              },
              { title: 'Quality Grade',
                type: 'select2',
                items: ['Excellent','Good','Fair','Poor'],
                value: 'Good'
              },
              //{ title: 'Daily People Hours',
              //  type: 'input',
              //  value: 2000
              //}
            ]
          },
          {
            active: 1,
            action: 'waves',
            title: 'Water Management',
            items: [
              { title: 'Rain Catchment Area (sqft)',
                type: 'input',
                value: 10000
              },
              { title: 'Cistern Volume (gallons)',
                type: 'input',
                value: 20000
              },
              { title: 'Toilet Type',
                type: 'select',
                items: [{text: 'Foam Flush Composting', value: 0.04},{text: '3x Foam Flush Composting', value: 0.12},{text: 'Ultra High Efficiency',value: 0.8},{text: 'High Efficiency',value: 1.28},{text: 'Construction Grade', value: 1.6}],
                value: {text: 'Foam Flush Composting', value: 0.04},
                efficiency: 0.04,
                units: 'gal',
                color: 'amber lighten-5'
              },
              { title: 'Faucet Type',
                type: 'select',
                items: [{text:'Ultra High Efficiency',value: 0.09},{text: 'High Efficiency',value: 0.13}],
                value: {text:'Ultra High Efficiency',value: 0.09},
                efficiency: 0.09,
                units: 'gal',
                color: 'transparent'
              },
            ]
          },
          {
            active: 1,
            action: 'battery_charging_full',
            title: 'Energy Management',
            items: [
              { title: 'Solar Array Power (kW-DC)',
                type: 'input',
                value: 330
              },
              { title: 'Lightbulb Type',
                type: 'select',
                items: [{text: 'LED', value: 9.5},{text: 'CFL', value: 13},{text: 'Incandescent',value: 60}],
                value: {text: 'LED', value: 9.5},
                efficiency: 9.5,
                units: 'Watts',
                color: 'transparent'
              },
              { title: 'HVAC Type',
                type: 'select2',
                items: [{text: 'VAV + Radiant Floors', value: 0.04},{text: 'VAV only',value: 0.8}],
                value: {text: 'VAV + Radiant Floors', value: 0.04},
                efficiency: 0.04,
                units: 'Watts',
                color: 'transparent'
              },
              { title: 'Windows',
                type: 'select',
                items: [{text:'Single Pane',value: 100},{text: 'Double Pane',value: 80},{text: 'Triple Pane',value: 60}],
                value: {text:'Triple Pane',value: 60},
                efficiency: 60,
                units: '% Energy Loss',
                color: 'transparent'
              },
            ]
          },
        ],
  }),
  methods: {
    display(msg){
      console.log(msg)
    },
    inputChange(subItem){
      if(subItem.title == 'Toilet Type'){
        console.log('Changing Toilet Type');
        this.$store.commit('changeToiletType',this.items[2].items[0]);
        // console.log(this.$store.state.water.toilet)
        // console.log(this.$store.state.water.kFactors.toilets)
      }else if(subItem.title == 'Faucet Type'){
        console.log('Changing Faucet Type');
        this.$store.commit('changeFaucetType',this.items[2].items[1]);
        console.log(this.$store.state.water.kFactors.toilets)
      }else if(subItem.title == 'Lightbulb Type'){
        console.log('Changing Lightbulb Type');
        this.$store.commit('changeLightbulbType',this.items[3].items[0]);
        console.log(this.$store.state.energy.kFactors.lightbulb)
      }else if(subItem.title == 'HVAC Type'){
        console.log('Changing HVAC Type');
        this.$store.commit('changeHVACType',this.items[3].items[1]);
        console.log(this.$store.state.energy.kFactors.hvac)
      }else if(subItem.title == 'Windows'){
        console.log('Changing Window Type');
        this.$store.commit('changeWindowType',this.items[3].items[2]);
        console.log(this.$store.state.energy.kFactors.window)
      }else if(subItem.title == 'Cistern Volume (gallons)'){
        console.log('Changing Cistern Size');
        this.$store.commit('changeCisternSize',this.items[1].items[1]);
        console.log(this.$store.state.water.cisternCapacity)
      }
    },
  },
  // watch: {
  //   items: function(newItem,oldItem){
  //     this.inputChange(newItem)
  //   },
  // },
  computed: {
  },
  beforeMount() {
    // this.drawer = this.$vuetify.breakpoint.lgAndUp
  },
  mounted: function(){

  }
}
</script>
